import Burger from './iconimo.burger';
import Close from './iconimo.close';
import ArrowLeft from './iconimo.arrow.left';
import ArrowRight from './iconimo.arrow.right';
import ArrowUp from './iconimo.arrow.up';

class IconimoIconset {

    constructor() {
        this.icons = {
            burger : Burger,
            close : Close,
            arrow_left : ArrowLeft,
            arrow_right : ArrowRight,
            arrow_up : ArrowUp,
        }
    }

    get(icon) {
        if(typeof this.icons[icon] === 'function') {
            return this.icons[icon];
        } else {
            throw new Error('Icon \''+icon+'\' not found in the list.');
        }
    }

}

export default IconimoIconset;


